<template>
  <div class="faqPopup">
    <div class="popupBg"></div>
    <div class="popupDesc">
      <div class="faqHeader">
        <h1>FAQ</h1>
        <div class="xBtn">
          <div class="svgBox">
            <svg
              id="Layer_1"
              data-name="Layer 1"
              xmlns="http://www.w3.org/2000/svg"
              xmlns:xlink="http://www.w3.org/1999/xlink"
              viewBox="0 0 36 36"
            >
              <title>Shape 1</title>
              <image
                width="36"
                height="36"
                xlink:href="data:image/png;base64,"
              />
            </svg>
          </div>
        </div>
      </div>
      <div class="faqNav">
        <ul>
          <li class="rowNav">
            <span :class="{ active:isChat}" @click="navActive(1)">PASS 금융사기방지란 ?</span>
          </li>
          <li>
            <span :class="{active : isTip}" @click="navActive(2)">보이스피싱 예방 TIP</span>
          </li>
          <li>
            <span :class="{ active : isPrivacy }" @click="navActive(3)">개인정보보호 TIP</span>
          </li>
        </ul>
      </div>

      <div class="contentBox" :style="{ height:deviceHeight+'px'}">
        <faq-chat v-if="isChat"></faq-chat>
        <faq-tip v-if="isTip"></faq-tip>
        <faq-privacy v-if="isPrivacy"></faq-privacy>
      </div>
    </div>
  </div>
</template>   
<script>
import FaqChat from '@/components/Faq/FaqChat.vue';
import FaqTip from '@/components/Faq/FaqTip.vue';
import FaqPrivacy from '@/components/Faq/FaqPrivacy.vue';
export default {
	data() {
		return {
			deviceHeight: 0,
			isChat: true,
			isTip: false,
			isPrivacy: false,
		};
	},
	components: {
		FaqChat,
		FaqTip,
		FaqPrivacy,
	},
	methods: {
		navActive(index) {
			switch (index) {
				case 1:
					this.isChat = true;
					this.isTip = false;
					this.isPrivacy = false;
					break;
				case 2:
					this.isChat = false;
					this.isTip = true;
					this.isPrivacy = false;
					break;
				case 3:
					this.isChat = false;
					this.isTip = false;
					this.isPrivacy = true;
					break;
			}
		},
	},
	mounted() {
		this.deviceHeight = document.documentElement.clientHeight * 0.9 - 160;
	},
};
</script>

